@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
}

a {
    text-decoration: none;
}

img:not(.imagen-tabla, .pic-small),
picture {
    width: 100%;
    display: block;
}

