@use 'sass:color';
@use '../base/variables' as v;
@use '../base/mixins' as m;


.formulario {
    
    fieldset {
        background-color: color.adjust(v.$gris, $lightness: +8%);

        @include m.tablet {
            width: 60%;
            margin-left: 20%; 
        }
        
    }
    p {
        font-size: 1.4rem;
        color: v.$grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: v.$grisOscuro;
    }

    label {
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
    input[type="submit"] {
        @include m.tablet {
            margin-left: 20%;
        }
    }
}