@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenido-nosotros {
    @include m.tablet {
        @include m.grid(2, 2rem);
    }
}

.texto-nosotros blockquote {
    font-weight: v.$black;
    font-size: 2.2rem;
    margin: 3rem;
    padding-left: 1rem 0 3rem 0;
    @include m.tablet {
        margin: 0;
    }
}