html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

a {
  text-decoration: none;
}

img:not(.imagen-tabla, .pic-small),
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.admin-nav {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .admin-nav {
    grid-template-columns: repeat(3, 1fr);
  }
}

.admin-opt {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  margin: 5rem 2rem;
}
@media (min-width: 768px) {
  .admin-opt {
    grid-template-columns: repeat(2, 1fr);
  }
}

#hoydia {
  text-align: right;
  color: #e6a775;
  font-size: 2rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .alerta {
    width: 60%;
    margin-left: 20%;
  }
}
.alerta__error {
  background-color: #f8aaaa;
  color: #e20101;
  border: solid 3px #fe0404;
  border-radius: 1rem;
  text-shadow: #333333 1px 0.5px;
}
.alerta__exito {
  background-color: #474747;
  color: #e6a775;
  border: solid 3px #e6e5dc;
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f1f0eb;
  border-radius: 1rem;
  border-bottom: 1px solid #d5d3c4;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 1rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #e6a775;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 2rem;
  margin-right: 0.6rem;
}
@media (min-width: 768px) {
  .iconos-caracteristicas li img {
    max-width: 10rem;
  }
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

.footer {
  padding: 1.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
  /*.contenedor-footer {
      padding-top: v.$separacion * 0.5;
  }*/
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0.8rem;
}

.formulario fieldset {
  background-color: #f7f7f4;
}
@media (min-width: 768px) {
  .formulario fieldset {
    width: 60%;
    margin-left: 20%;
  }
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e6e5dc;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
@media (min-width: 768px) {
  .formulario input[type=submit] {
    margin-left: 20%;
  }
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
  filter: invert(61%) sepia(78%) saturate(331%) hue-rotate(339deg) brightness(103%) contrast(91%);
}
.icono h3 {
  text-transform: uppercase;
}

.muestra {
  font-family: FontAwesome;
  font-size: 1.3rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
  border-bottom: none;
}

.informacion-meta span {
  color: #e6a775;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 25rem;
  height: 0.5rem;
  background-color: #CB4E47;
  margin-top: 1rem;
}

.amarillo {
  color: #e6a775;
}

.gris {
  background-color: #f9f9f7;
  border-radius: 1rem;
  padding: 1.5rem;
  border-bottom: 0.2rem solid #f1f0eb;
  margin: 0;
}

@keyframes breathing {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 hsl(var(--primary));
  }
  50% {
    transform: scale(1.03);
    box-shadow: 0 0 20px 5px hsl(var(--primary)/0.3);
  }
  to {
    transform: scale(1);
    box-shadow: 0 0 0 0 hsl(var(--primary));
  }
}
.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 2.8rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
.derecha .dark-mode-boton:active {
  color: #ededed;
  transform: scale(0.6) rotateZ(50deg) !important;
  transition: all 0.1s ease-in-out !important;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra img:hover {
    animation: breathing 2s ease-in-out infinite;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
    transition: transform 0.3s ease;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  transform: translateY(-5px);
  /* Desplazamiento hacia arriba */
  color: #e6a775;
  text-decoration: underline #e6a775 3px;
  text-underline-offset: 15px;
}

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu:active {
  transform: scale(0.7, 0.7);
  transition: all 0.1s ease-in-out;
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.testimoniales {
  margin-top: 1rem;
}

.testimonial {
  background-color: #e6a775;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

table.propiedades {
  margin-top: 5rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: rgba(101, 101, 101, 0.9960784314);
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades td {
  text-align: center;
  align-items: center;
}
table.propiedades tr:nth-child(2n) {
  background-color: #f5f4f1;
}
table.propiedades .imagen-tabla {
  width: 12rem;
}

.pic-small {
  width: 30rem;
}

.tabla-contenido {
  visibility: hidden;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem;
  padding-left: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .texto-nosotros blockquote {
    margin: 0;
  }
}

