@use 'sass:color';
@use '../base/variables' as v;
@use '../base/mixins' as m;

@keyframes breathing {
    0% {
        transform: scale(1);
        box-shadow: 0 0 0 0 hsl(var(--primary))
    }

    50% {
        transform: scale(1.03);
        box-shadow: 0 0 20px 5px hsl(var(--primary)/.3)
    }

    to {
        transform: scale(1);
        box-shadow: 0 0 0 0 hsl(var(--primary))
    }
}

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        align-items: flex-end;
    }

    .dark-mode-boton {
        width: 2.8rem;
        margin-top: 3rem;
        margin-bottom: 2rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }
        &:active {
            color: color.adjust(v.$blanco, $lightness: -7%);
                transform: scale(0.6) rotateZ(50deg) !important;
                transition: all 0.1s ease-in-out !important;
            
        }

        @include m.tablet {
            margin-top: 0;
        }
    }
    
}
.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        
        img {
            &:hover {
            animation: breathing 2s ease-in-out infinite
        }
        }
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;

    @include m.tablet {
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
        transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    }
    a {
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;

        @include m.tablet {
            margin-right: 2rem;
            font-size: 1.8rem;
            transition: transform 0.3s ease;

            &:last-of-type {
                margin-right: 0;
            }
        }

        &:hover {
            transform: translateY(-5px);
                /* Desplazamiento hacia arriba */
                color: v.$amarillo;
                text-decoration: underline v.$amarillo 3px;  
                text-underline-offset: 15px;
        }
    }
}

/** Menu de hamburguesa **/
.mobile-menu {
    @include m.tablet {
        display: none;
    }
&:active {
        transform: scale(.7, .7);
        transition: all 0.1s ease-in-out;
        }
    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}